.user-block {

  .user-link{
    justify-content:space-between;
    margin: 0 3px;
  }

  .logged-in{
    position: relative;

    button{
      text-align: left;
      border: none;
      background: #fff;

      .fa{
        margin: 0 5px -1px 5px;
      }

      .fa-chevron-up{
        display: none;
      }
    }

    button:focus{
      outline: none;
    }

    .dropdown-menu{
      display: none;
    }

    .open {
      .dropdown-menu{
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: 1000;
        top:26px;
        background: #fff;
        padding: 10px;
        width: 150px;
        min-width: 100px;
        line-height: 1.5em;
        border-bottom: solid 1px #ccc;
        border-left: solid 1px #ccc;
        border-right: solid 1px #ccc;
      }

      .fa-chevron-down{
        display: none;
      }

      .fa-chevron-up{
        display: inline-block;
      }
    }
  }

}